import React, {useState} from 'react'
import{AppBar, Box, IconButton, Drawer,Divider, Toolbar, Typography,} from '@mui/material'
import FastfoodIcon from '@mui/icons-material/Fastfood';
import {NavLink} from 'react-router-dom'
import MenuIcon from '@mui/icons-material/Menu';
import logo from '../../images/logo.svg'
const Header = () => {
  const[mobileOpen,setMobileOPen]= useState(false)
//handelmenu click
const handelDrawerToggle =() => {
  setMobileOPen(!mobileOpen)
}
  //menu drawewr
  const drawer=(
    <Box onClick={handelDrawerToggle} sx={{textAlign:'center'}}>
<Typography color={'goldenrod'} variant="h6" component="div" sx={{ flexGrow:1,my:2}}>
        <img src={logo} alt="logo" height={'70'} width="250" />
      </Typography>
      <Divider/>
    <ul className="mobile-navigation">
    <li>
      <NavLink activeclassName="active" to={'/'}>Home</NavLink>
    </li>
    <li>
      <NavLink to={'/Menu'}>Menu</NavLink>
    </li>
    <li>
      <NavLink to={'/About'}>About</NavLink>
    </li>
    <li>
      <NavLink to={'/Contact'}>Contact</NavLink>
    </li>
    </ul>
     
    </Box>
  )
  return (
    <>
    <Box>
    <AppBar component={"nav"} sx={{ bgcolor:"black"}}>
      <Toolbar>
      <IconButton color="inherit"
       aria-label="open drawer" 
       edge="start"
      sx={{mr:2, display:{sm: "none"},
      }}
      onClick={handelDrawerToggle}
      >
        <MenuIcon/>
      </IconButton>
      <Typography color={'goldenrod'} variant="h6" component="div" sx={{ flexGrow:1}}>
      <img src={logo} alt="logo" height={'70'} width="250" />
      </Typography>

      <Box sx={{display:{ xs:"none",sm:"block"}}}>
    <ul className="navigation-menu">
    <li>
      <NavLink activeclassName="active" to={'/'}>Home</NavLink>
    </li>
    <li>
      <NavLink to={'/Menu'}>Menu</NavLink>
    </li>
    <li>
      <NavLink to={'/About'}>About</NavLink>
    </li>
    <li>
      <NavLink to={'/Contact'}>Contact</NavLink>
    </li>
    </ul>
      </Box>
      </Toolbar>
     
    </AppBar>
    <Box component="nav">
<Drawer variant="temporary" open={mobileOpen} onClose={handelDrawerToggle}
sx={{display:{sx:'block',sm:'none'},"&.MuiDrawer-paper":{boxSizing:"border-box",width:"240px",},}}>
  {drawer}
</Drawer>

    </Box>
    <Box>
    <Toolbar/>
    </Box>
    </Box>
    </>
  );
};

export default Header

